import { ReactElement, useCallback, useEffect, useMemo, useState } from "react";

import { IJobContent } from "@core/api-types/entities/job";

export type ResolvedContent = Readonly<IJobContent & { contents: any }>;

type ChildrenArgs = Readonly<{
  contents: ReadonlyArray<ResolvedContent>;
  failed: boolean;
  progress: Readonly<{
    current: number;
    total: number;
  }>;
  ready: boolean;
}>;

type Props = Readonly<{
  allowFailures?: ReadonlyArray<string>;
  auth: string;
  children: (args: ChildrenArgs) => ReactElement;
  content: ReadonlyArray<IJobContent>;
  maxFetchAttempts?: number;
}>;

const fetchContent = async (
  auth: string,
  content: IJobContent,
  allowFailures: ReadonlyArray<string>,
): Promise<any> => {
  const result = await fetch(content.uri, {
    headers: {
      Authorization: `Basic ${auth}`,
    },
  });
  if (result.ok) {
    switch (content.type) {
      case "application/json": {
        try {
          const decoded = await result.json();
          return decoded;
        } catch (err) {
          if (allowFailures.includes(content.role)) {
            return null;
          }
          throw new Error(`Job content for: ${content.id} is invalid JSON.`);
        }
      }
      case "image/png": {
        const buff = await result.arrayBuffer();
        const bytes = new Uint8Array(buff);
        let str = "";
        bytes.forEach(b => {
          str += String.fromCharCode(b);
        });
        return `data:image/png;base64, ${btoa(str)}`;
      }
      case "application/xml":
      default:
        return result.text();
    }
  }
  if (allowFailures.includes(content.role)) {
    return null;
  }
  throw new Error(`Unable to retrieve job content for: ${content.id}`);
};

const fetchParallelContent = async (
  auth: string,
  contentList: ReadonlyArray<IJobContent>,
  retrievedContent: Array<any>,
  onRetrieveContent: (index: number, result: any) => void,
  allowFailures: ReadonlyArray<string>,
): Promise<void> => {
  await Promise.all(
    contentList.map(async (content, index) => {
      if (retrievedContent[index] !== undefined) {
        return Promise.resolve();
      }
      try {
        const result = await fetchContent(auth, content, allowFailures);
        onRetrieveContent(index, result);
      } catch (err) {
        onRetrieveContent(index, undefined);
      }
    }),
  );
};

export const ContentLoader = ({
  allowFailures,
  auth,
  children,
  content,
  maxFetchAttempts,
}: Props) => {
  const initialState: Array<any> = useMemo(() => {
    return new Array(content.length).fill(undefined);
  }, [content.length]);
  const [contentState, setContentState] = useState<Array<any>>(initialState);
  const [completed, setCompleted] = useState(false);
  const [fetching, setFetching] = useState(false);
  const [fetchCount, setFetchCount] = useState(0);
  const [failed, setFailed] = useState(false);
  const onRetrieve = useCallback(
    (index: number, result: any) => {
      setContentState(s => [...s.slice(0, index), result, ...s.slice(index + 1)]);
    },
    [setContentState],
  );
  useEffect(() => {
    if (failed || completed || fetching) {
      return;
    }
    const unfetched = contentState.some(c => c === undefined);
    if (!unfetched) {
      setCompleted(true);
      setFetching(false);
      return;
    }
    if (fetchCount >= (maxFetchAttempts || 5)) {
      setFailed(true);
    }
    setFetching(true);
    setFetchCount(fc => fc + 1);
    fetchParallelContent(
      auth,
      content,
      contentState,
      onRetrieve,
      Array.isArray(allowFailures) ? allowFailures : [],
    ).then(() => {
      setFetching(false);
    });
  }, [contentState, fetching]);
  const response: ChildrenArgs = useMemo(() => {
    const filled = contentState.filter(s => s !== undefined).length;
    return {
      contents: content.map(
        (c, i): ResolvedContent => ({
          ...c,
          contents: contentState[i],
        }),
      ),
      failed,
      progress: { current: filled, total: contentState.length },
      ready: filled >= contentState.length,
    };
  }, [contentState, failed]);
  return children(response);
};
ContentLoader.displayName = "ContentLoader";
