import React, { memo } from "react";

import { mapToApiPage } from "./helpers";
import { PagePreview } from "./PagePreview";
import { Renderer } from "./Renderer";
import { Size } from "./Renderer/types";
import { StateManager } from "./StateManager";
import { IApiPage, IApiZone, URI } from "./types";

type Props = Readonly<{
  highConfidenceThreshold: number;
  highResImageURIs: Array<URI>;
  initialZones: Array<Array<IApiZone>>;
  jobId: string;
  lowResImageURIs: Array<URI>;
  pageConfidences: Array<number>;
  pageDimensions: Array<Size>;
  onSave: (pages: Array<IApiPage>, modifiedPages: Array<number>) => Promise<void>;
  review: boolean;
  saving: boolean;
  startingPage: number;
}>;

export const Zoning = memo(
  ({
    highConfidenceThreshold,
    highResImageURIs,
    initialZones,
    jobId,
    lowResImageURIs,
    pageConfidences,
    pageDimensions,
    onSave,
    review,
    saving,
    startingPage,
  }: Props) => (
    <StateManager
      initialZones={initialZones}
      jobId={jobId}
      pageDimensions={pageDimensions}
      review={review}
      startingPage={startingPage}
    >
      {({ dispatch, persistedZones, state }) => (
        <div className="zoning">
          <div className="contents">
            <Renderer
              dispatch={dispatch}
              onDelete={() => {
                // simulate the user pressing "D"
                dispatch({
                  ctrlKey: false,
                  key: "d",
                  keyCode: 68,
                  // tslint:disable-next-line: no-empty
                  preventDefault: () => {},
                  shiftKey: false,
                  type: "KEY PRESSED",
                });
              }}
              onSave={() => {
                onSave(
                  state.pages.map(mapToApiPage),
                  state.pages.reduce((prev, page, index) => {
                    if (page.modified) {
                      prev.push(index);
                    }
                    return prev;
                  }, [] as Array<number>),
                );
              }}
              pageImageURIs={highResImageURIs}
              review={review}
              saving={saving}
              state={state}
            />
            <PagePreview
              activePage={state.activePage}
              dispatch={dispatch}
              highConfidenceThreshold={highConfidenceThreshold}
              onSelectPage={index => {
                dispatch({
                  page: index,
                  type: "SELECT PAGE",
                });
              }}
              pageConfidences={pageConfidences}
              pageImageURIs={lowResImageURIs}
              pages={state.pages}
            />
          </div>
        </div>
      )}
    </StateManager>
  ),
);
Zoning.displayName = "Zoning";
