import React from "react";

import { IJob } from "@core/api-types/entities/job";
import { ContentLoader } from "@ui/components/util/ContentLoader";
import { Loading } from "@ui/components/util/Loading";
import { Zoning } from "@ui/components/Zoning";

type Props = Readonly<{
  job: IJob;
  token: string;
}>;

const CONTENT_ROLE_LIST = [
  "zoning-page-png",
  "zoning-pagehr-png",
  "zoning-pagesmeta-json",
  "zoning-reviewed-json",
];

export const ZoningReview = ({ job, token }: Props) => {
  if (!job || !token) {
    return null;
  }
  const contentList = job.contents.filter(c => CONTENT_ROLE_LIST.indexOf(c.role) >= 0);
  return (
    <ContentLoader auth={token} content={contentList}>
      {({ contents, failed, progress, ready }) => {
        if (failed) {
          const error = new Error(
            "Failed to load job. Please report this error to a system administrator.",
          );
          return <Loading error={error} text={"Job failed to load."} />;
        }
        if (!ready) {
          return <Loading progress={progress} text={"Fetching job contents."} />;
        }
        const lowResImageURIs = contents
          .filter(c => c.role === "zoning-page-png")
          .map(c => c.contents);
        const highResImageURIs = contents
          .filter(c => c.role === "zoning-pagehr-png")
          .map(c => c.contents);
        const initialZones = contents
          .filter(c => c.role === "zoning-reviewed-json")
          .map(c => c.contents.predictions);
        const pageConfidences = contents
          .filter(c => c.role === "zoning-reviewed-json")
          .map(c => c.contents)
          .map(c => parseFloat(c.page_confidence) || 0);
        const pageDimensions = contents
          .find(c => c.role === "zoning-pagesmeta-json")!
          .contents.pages.map((p: any) => ({ height: p.images.height, width: p.images.width }));
        return (
          <Zoning
            highConfidenceThreshold={
              Number.parseFloat(job.metadata.high_confidence_threshold) || 1.0
            }
            highResImageURIs={highResImageURIs}
            initialZones={initialZones}
            jobId={job.id}
            lowResImageURIs={lowResImageURIs}
            onSave={async () => {
              console.warn("This should never be called.");
            }}
            pageConfidences={pageConfidences}
            pageDimensions={pageDimensions}
            review
            saving={false}
            startingPage={0}
          />
        );
      }}
    </ContentLoader>
  );
};
ZoningReview.displayName = "ZoningReview";
