import { ReactElement, useEffect, useMemo, useReducer, useState } from "react";

import { getPersistentState, setPersistentState } from "../../../util/persistState";
import { newPage } from "../helpers";
import { Size } from "../Renderer/types";
import { IApiZone, IZone } from "../types";
import { reducer } from "./reducer";
import { Action, IState, ZoomLevel } from "./state";

export const DEFAULT_INITIAL_STATE: IState = {
  activePage: 0,
  autoShrink: true,
  currentLabel: "text",
  pages: [],
  zoom: ZoomLevel["1x"],
};

interface IChildrenArgs {
  readonly dispatch: (action: Action) => void;
  readonly persistedZones: Array<Array<IZone> | null> | null;
  readonly state: IState;
}

type Props = Readonly<{
  children: (args: IChildrenArgs) => ReactElement;
  initialZones: Array<Array<IApiZone>>;
  jobId: string;
  pageDimensions: Array<Size>;
  review: boolean;
  startingPage: number;
}>;

const LOAD_PERSISTED_STATE_MESSAGE = "Would you like to load your previous work for this job?";
const STATE_APP_ID = "zoning-workbench";
const STATE_VERSION = "1";

export const StateManager = ({
  children,
  initialZones,
  jobId,
  pageDimensions,
  review,
  startingPage,
}: Props) => {
  console.log("Initial zones:", initialZones);
  const initialState: IState = useMemo(
    () => ({
      ...DEFAULT_INITIAL_STATE,
      activePage: startingPage,
      pages: pageDimensions.map((dimension, index) =>
        newPage(index, dimension, initialZones[index]),
      ),
    }),
    [initialZones, startingPage],
  );
  const [state, dispatch] = useReducer(reducer, initialState);
  const [isFirstRun, setFirstRun] = useState(true);
  const [persistedZones, setPersistedZones] = useState<IChildrenArgs["persistedZones"]>(null);
  const zones = state.pages.map(p => p.zones);
  const zonesAsString = JSON.stringify(zones);
  useEffect(() => {
    if (!isFirstRun || persistedZones !== null || review) {
      return;
    }
    setFirstRun(false);
    const [, persistedState] = getPersistentState(STATE_APP_ID, jobId);
    if (persistedState !== null) {
      if (JSON.stringify(persistedState) === zonesAsString) {
        return;
      }
      if (window.confirm(LOAD_PERSISTED_STATE_MESSAGE)) {
        setPersistedZones(persistedState as Array<Array<IZone> | null>);
      }
    }
  });
  useEffect(() => {
    if (isFirstRun) {
      return;
    }
    setPersistentState(STATE_APP_ID, jobId, STATE_VERSION, zones);
  }, [zonesAsString]);
  return children({ dispatch, persistedZones, state });
};
