import React from "react";
import { HashRouter, Route, Switch } from "react-router-dom";

import { Loading } from "@ui/components/util/Loading";
import { JobManager } from "./JobManager";
import { ZoningReview } from "./ZoningReview";

export const App = () => {
  return (
    <div className="overflow-hidden vh-100 vw-100">
      <HashRouter>
        <Switch>
          <Route path="/:token/jobs/:jobId">
            <JobManager>
              {({ job, jobError, token }) => {
                if (jobError) {
                  return <Loading error={jobError} text="Job failed to load." />;
                } else if (job === null) {
                  return <Loading text="Fetching job details." />;
                }
                return <ZoningReview job={job} token={token} />;
              }}
            </JobManager>
          </Route>
          <Route path="*">
            <p>Page not found.</p>
          </Route>
        </Switch>
      </HashRouter>
    </div>
  );
};
App.displayName = "App";
