import React from "react";
import { Rect } from "react-konva";

import { Point, Size } from "./types";

interface IProps {
  readonly label: string;
  readonly point: Point;
  readonly size: Size;
}

const COLOR_LEGEND = {
  image: {
    fill: "#FF00FF",
  },
  math: {
    fill: "#8B4513",
  },
  table: {
    fill: "#FFA500",
  },
  text: {
    fill: "#0000FF",
  },
  unknown: {
    fill: "#000000",
  },
};

export const Zone = ({ label, point, size }: IProps) => {
  let colors = COLOR_LEGEND.unknown;
  if (label in COLOR_LEGEND) {
    colors = COLOR_LEGEND[label];
  }
  return (
    <>
      <Rect fill={colors.fill} strokeWidth={0.5} {...point} {...size} opacity={0.2} />
    </>
  );
};
Zone.displayName = "Zone";
